import { action } from "mobx";

const changeBalance = action((item, balance, bet) => {
  if (item.type === 1) {
    if (balance >= bet * 4) {
      balance -= bet * 4;
    } else {
      balance = 0;
    }
  } else if (item.type === 2) {
    balance += bet * 4;
  } else if (item.type === 3) {
    if (balance >= bet) {
      balance -= bet;
    } else {
      balance = 0;
    }
  } else {
    balance += bet * 3;
  }
  return balance;
});

export default changeBalance;
