import React, { useState, useEffect } from "react";
import WebApp from "@twa-dev/sdk";
import { getRandomKey } from "../../utils/getRandomKey";
import { shuffle } from "../../utils/shuffle";
import FieldItem from "../../components/FieldItem";
import changeBalance from "../../utils/changeBalance";
import { action } from "mobx";
import diamond from "../../assets/audio/diamond.mp3";
import fire from "../../assets/audio/fire.mp3";
import ghost from "../../assets/audio/ghost.mp3";
import Lottie from "lottie-react";
import Firework from "../../assets/animation/Firework.json";

const defaultItems = [
  {
    id: getRandomKey(),
    type: 1,
    isOpened: false,
  },
  {
    id: getRandomKey(),
    type: 1,
    isOpened: false,
  },
  {
    id: getRandomKey(),
    type: 1,
    isOpened: false,
  },
  {
    id: getRandomKey(),
    type: 2,
    isOpened: false,
  },
  {
    id: getRandomKey(),
    type: 2,
    isOpened: false,
  },
  {
    id: getRandomKey(),
    type: 2,
    isOpened: false,
  },
  {
    id: getRandomKey(),
    type: 3,
    isOpened: false,
  },
  {
    id: getRandomKey(),
    type: 3,
    isOpened: false,
  },
  {
    id: getRandomKey(),
    type: 3,
    isOpened: false,
  },
];

let shuffledItems = shuffle([...defaultItems]);

const Field = action(
  ({ balance, updateBalance, bet, sound, handleButtonClick }) => {
    const [lastClickTime, setLastClickTime] = useState(null);

    const [items, setItems] = useState(shuffledItems);

    const [isClicked, setIsClicked] = useState(false);
    let timeId;
    let audio;

    const open = (item) => {
      if (bet > balance || balance < 10) return;
      if (isClicked) return;
      item.isOpened = true;

      const ghostLenght = items.filter(
        (item) => (item.type === 3) & item.isOpened
      ).length;
      const currentTime = new Date().getTime();
      setLastClickTime(currentTime);
      updateBalance(changeBalance(item, balance, bet));
      setItems([...items]);

      if (item.type === 1) {
        // audio = new Audio(fire);
        // audio.play();
      } else if (item.type === 2) {
        if (sound) {
          audio = new Audio(diamond);
          audio.play();
        }

        handleButtonClick();
      } else {
        if (sound) {
          audio = new Audio(ghost);
          audio.play();
        }
      }

      if (
        item.type === 1 ||
        item.type === 2 ||
        (item.type === 3) & (ghostLenght === 3)
      ) {
        if (ghostLenght === 3) {
          updateBalance(changeBalance((item = 4), balance, bet));
        }
        setIsClicked(true);
        timeId = setTimeout(() => {
          items.forEach((item) => (item.isOpened = true));
          setItems([...items]);
        }, 500);
        const timeoutDuration = 1.2 * 1000;
        setTimeout(() => {
          restart();
        }, timeoutDuration);
      }
    };

    const [secondsClick, setSecondsClick] = useState(30);
    const [isActiveClick, setIsActiveClick] = useState(false);

    useEffect(() => {
      let intervalId;

      if (isActiveClick) {
        intervalId = setInterval(() => {
          setSecondsClick((prevSecondsClick) => prevSecondsClick - 1);
        }, 1000);
      }

      return () => clearInterval(intervalId);
    }, [isActiveClick]);

    useEffect(() => {
      if (secondsClick === 0) {
        setIsActiveClick(false);
        setSecondsClick(30);
      }
    }, [secondsClick]);

    const restart = () => {
      clearTimeout(timeId);

      shuffledItems = shuffle([...defaultItems]);

      items.forEach((item) => (item.isOpened = false));
      setItems([...items]);

      setTimeout(() => {
        setItems(shuffledItems);
        setIsClicked(false);
      }, 400);
    };

    return (
      <div>
        <div className="root-container Oj3d mt-[100px]">
          <div className="field-container">
            <div>
              <ul className="field grid-gap scale-animation">
                {items.map((item) => {
                  return (
                    <FieldItem
                      key={item.id}
                      items={items}
                      item={item}
                      bet={bet}
                      onClick={() => {
                        open(item);
                        {
                          balance >= bet
                            ? WebApp.platform === "ios"
                              ? WebApp.HapticFeedback.impactOccurred(`medium`)
                              : WebApp.HapticFeedback.notificationOccurred(
                                  "success"
                                )
                            : WebApp.HapticFeedback.notificationOccurred(
                                "error"
                              );
                        }
                      }}
                    />
                  );
                })}
              </ul>
            </div>
          </div>
        </div>
      </div>
    );
  }
);

export default Field;
